<template>
	<view >
		<uni-section title="我的状态" type="line">
			<view class="uni-px-5">
				<text class="uni-pb-5">我的状态:</text>
				<uni-data-select 
					v-model="value" 
					:localdata="range" 
					@change="change"
					 :clear="false"
					placeholder="我的状态"
					style="width:60vw;!important">
				</uni-data-select>
			</view>
		</uni-section>
		
		<view>
			<!-- 输入框示例 -->
			<uni-popup ref="inputDialog" type="dialog">
				<uni-popup-dialog ref="inputClose" mode="input" title="自定义状态" 
					placeholder="请输入状态" @close="onCancel" @confirm="dialogInputConfirm"></uni-popup-dialog>
			</uni-popup>
		</view>
	</view>
</template>

<script>
export default {
	name:"privateMystatus",
	data() {
		let user = {};
		return {
			value: 1,
			range: [
				{value : -1 ,text:"自定义"},
			],
			length: 1,
		}
	},
	watch: {
	    value: {
	        handler (newValue, oldValue) {
				if(newValue == -1) {
					this.$refs.inputDialog.open()
				}
	        },
	        immediate: true
	    }
	},
	mounted() {
		this.getUserInfo();
	},
    methods: {
		// 拿到个人数据
		getUserInfo() {
			this.user = uni.getStorageSync("personInfo");
			this.value = parseInt(this.user.value)
		},
		// 取消，关闭弹窗
		onCancel() {
		    this.$refs.inputDialog.close() // 隐藏弹窗
		},
		// 确认自定义信息
		dialogInputConfirm(val) {
			this.range.push({ value: this.length, text: val })
			this.value = this.length
			this.change(this.length)
			this.length ++
			// 关闭窗口后，恢复默认内容
			this.$refs.inputDialog.close()
		},
		// 修改id
		change(e) {
			if(e !== -1) {
				uni.showLoading({
					title: '修改中'
				})
			}
			
			setTimeout( () => {
				let mine = {};
				mine.id = this.user.id;
				mine.value = this.value;
				for(let i = 0 ;i < this.range.length;i ++) {
					if(e === this.range[i].value) {
						mine.status = this.range[i].text;
					}
				}
				this.$emit("getPrivateStatus",mine);
				uni.hideLoading()
			},500)
			
			//  uni.request({
			// 	url: 'http://127.0.0.1:4523/m1/3371748-0-default/status',
			// 	method:"POST",
			// 	dataType:"json",
			// 	data:mine,
			// 	success(res) {
			// 		console.log(res)
			// 	}
			// })
		},
	}
}
</script>

<style>
  .uni-px-5 {
	margin: 10rpx;
	padding-bottom: 10px;
	display: flex;
	width: 80vw;
	justify-content: space-around;
	align-items: center;
	/* background-color: yellow; */
  }

  .uni-pb-5 {
	display: inline-block;
	width: 24vw;
  }
  
  .photo {
	  display: inline-block;
	  width: 60rpx;
	  height: 60rpx;
  }
  
  .dialog-box {
  		padding: 10px;
	}
	
	.dialog-text {
			font-size: 14px;
			color: #333;
		}

</style>